
*{
  box-sizing: border-box;
}

body, h1, p {
  margin: 0;
}

.top-nav {
  display: grid;
  grid-auto-flow: column;
  justify-content: left;
  gap: 0.5rem;
  background-color: rgb(53, 140, 53);
}

.top-nav a{
  color: white;
  text-decoration: none;
  padding: 1rem 2rem;

  text-transform: uppercase;
  font-size: 1.5rem;
}

.top-nav a:hover {
  background-color: white;
  color: rgb(53, 140, 53);
}

.feitSkrift{
  font-weight: 700;
}

.bildeforside{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
}

.bildeforside img {
  height: 500px;
  width: 800px;
  object-fit: cover;
}

.overskriftP{
  display: flex;
  grid-auto-flow: column;
  justify-content: center;
  padding: 1rem 0 1rem 0;
  grid-column: span 3;
}

.oppskrifter {
  max-width: 1000px;
  margin: auto;
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(3,1fr);
  padding: 0 0 2rem 0;
}

.oppskrifter div{
  background-color: #eee;
  padding: 1rem;
}

.oppskrifter div img {
  width: 100%;
}

.overlay {
  border: 5px solid rgb(43, 111, 43);
  position: absolute;
  width: 800px;
  height: 530px;
  position: absolute;
  left: calc(50% - 400px);
  top: calc(60% - 290px);
  background-color: rgba(0,0,0,0.4);
  overflow: auto;
}

.xknap{
  float: right;
  color: black;
  font-weight: bold;
  background-color: white;
  padding: 0.2rem;
  border-radius: 50%;
}

.hvitbak{
  background-color: white;
  padding: 0.3rem;
}